.slant {
  background-color: #2f54eb;
  clip-path: polygon(0 6vw, 100% 0, 100% 100%, 0 100%);
  padding-top: 12vw;
}
.container {
  margin-bottom: 140px;
}

.title {
  font-size: 38px;
  line-height: 46px;
  color: white;
  text-align: center;
  font-weight: 400;
  margin: 30px;
  margin-bottom: 140px;
}

.imageIcon {
  width: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-top: 38px;
}

.circle {
  width: 150px;
  height: 150px;
  background: white;
  -moz-border-radius: 75px;
  -webkit-border-radius: 75px;
  border-radius: 75px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom:30px
}

.col {
  text-align: center;
  font-size: 20px;
  color: white;
}
