.bigContainer {
  padding-top: 140px;
  padding-bottom: 120px;
  background-color: white;
  text-align: center;
}

.title {
  font-size: 38px;
  line-height: 46px;
  color: #314659;
  text-align: center;
  font-weight: 400;
  margin-bottom: 100px;
  padding-left: 30px;
  padding-right: 30px;
}
.container {
  position: relative;
  width: 200px;
  height: 200px;
  margin-bottom: 48px;
}

.image {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  box-shadow: 0 12px 20px #d8e0e6;
}

.imageTop {
  border-radius: 50%;
  width:100%;
  height: 100%;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
}

.container:hover .imageTop {
  display: inline
}

.text {
  opacity: 1;
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.position {
  font-size: 20px;
  line-height: 28px;
  margin-bottom: 8px;
  color: black;
}

.image2 {
  height: auto;
  width: 400px;
  margin-top: 72px;
}