
  .item {
    width: 90%;
    border-radius: 5px;
    height: 35px;
    margin-bottom: 10px;
    background: rgb(250, 250, 250);
  
    &[data-key='0'] {
        background: rgb(240, 240, 240);
      }
    
      &[data-key='1'] {
        background: rgb(220, 220, 220);
      }
    
      &[data-key='2'] {
        background: rgb(200, 200, 200);
      }
    
      &[data-key='3'] {
        background: rgb(180, 180, 180);
      }
      &[data-key='4'] {
        background: rgb(160, 160, 160);
      }
  }
