.container {
  background-color: #eff3f6;
  box-shadow: 0 12px 20px #d8e0e6;
}
.bigContainer {
  padding-top:140px;
  padding-bottom: 120px;
  background-color: #f0f3f6;
}

.title {
  font-size: 38px;
  line-height: 46px;
  color: #314659;
  text-align: center;
  font-weight: 400;
  margin-bottom: 100px;
  padding-left: 30px;
  padding-right: 30px;
}

.blue {
  background-color: #2f54eb;
  color: white;
  padding: 56px;
}

.white {
  background-color: white;
  color: #314659;
  padding: 56px;
}

.sectionTitleWhite {
  font-size: 28px;
  margin: 0 auto 32px;
  color: white;
}

.sectionText {
  color: #fff;
  line-height: 32px;
}

.rowTextContainer {
  height:100%;
  border-bottom: 1px solid #ebedf0;
  padding-top: 24px;
}

.rowTextContainerLast {
  height:100%;
  padding-top: 24px;
}

.rowTextTitle {
  font-size: 20px;
  line-height: 28px;
  margin-bottom: 8px;
  color: black;
}

.rowTextDescription {
  color: #314659;
  line-height: 24px;
}

.rowContainer {
  height: 25%;
}
.rowImageContainer {
  
  img {
    width: 100%;
    height: auto;
  }
}